보 형태의 글, 사진, 동영상 및 각종 파일과 링크 등을 의미합니다.")]),_c('h5',[_vm._v("8. “비밀번호”란 회원이 부여 받은 아이디와 일치된 회원임을 확인하고 회원의 권익 및 비밀보호를 위하여 회원 자신이 정한 문자와 숫자의 조합을 의미합니다.")]),_c('h5',[_vm._v("9. “운영자”란 서비스의 전반적인 관리와 원활한 운영을 위해 회사를 구성하는 구성원을 의미합니다.")]),_c('h5',[_vm._v("10. “제휴 사이트”란 회사가 업무제휴를 통해 공동 마케팅, 공동사업 등을 추진하기 위하여 하이퍼링크 방식(하이퍼 링크의 대상에는 문자, 정지 및 동화상 등이 포함됩니다) 등으로 연결한 업무제휴 사업체의 웹사이트를 의미합니다.")]),_c('h5',[_vm._v("11. “서비스의 제한•중지”란 공공의 이익 또는 회사의 규정에 위배되는 경우 회사가 회원에게 제공하는 서비스의 전부 또는 일부를 제한하거나 중지하는 것을 의미합니다.")]),_c('h5',[_vm._v("12. “탈퇴”란 회원이 서비스 이용계약을 자의로 종료시키는 것을 의미합니다.")]),_c('h5',[_vm._v("13. “해지”란 회사가 회원의 서비스 이용계약을 종료시키는 것을 의미합니다.")]),_c('h5',[_vm._v("14. “교원자격증”이란 회원이 회원의 특별한 자격을 요구할 때 교원의 자격을 증명할 수 있는, 공신력 있는 기관에서 발급된 정식 교원자격증을 의미합니다.")]),_c('h5',[_vm._v("15. “나이스 고유번호”란 NEIS 사이트에서 각 계정 별로 부여되는 고유번호를 의미합니다.")]),_c('h5',[_vm._v("16. “커뮤니티 운영규칙”이란 회사가 사이트의 원활한 운영을 위해 정해두는 규칙을 의미합니다.")]),_c('br'),_c('h4',[_vm._v("제 3조 [약관의 효력 및 개정]")]),_c('br'),_c('h5',[_vm._v("1. 본 약관의 내용과 회사의 상호, 운영자의 전자우편주소 등의 정보를 회사는 회원이 쉽게 알 수 있도록 Pedagy 서비스 화면을 통하여 이를 게시하거나 기타의 방법으로 회원에게 통지하고, 본 통지로써 그 효력이 발생합니다.")]),_c('h5',[_vm._v("2. 회사의 영업상 중요한 사유가 발생될 경우와 기타 회사가 필요하다고 인정하는 경우에 “약관의규제등에관한법률”, “정보통신망이용촉진및정보보호등에관한법률(이하 \"정보통신망법\"이라 합니다)” 등 관련법을 위배하지 않는 범위에서 본 약관을 개정할 수 있습니다.")]),_c('h5',[_vm._v("3. 회사가 약관을 개정할 경우에는 적용일자 및 개정사유를 명시하여 현행약관과 함께 그 적용일자 7일 이전부터 적용일자 전일까지 공지합니다. 다만, 회원에게 불리하게 약관내용을 변경하는 경우에는 최소한 30일 이상의 사전 유예기간을 두고 공지합니다. 이 경우 회사는 개정 전 내용과 개정 후 내용을 명확하게 비교하여 회원이 알기 쉽도록 표시합니다.")]),_c('h5',[_vm._v("4. 회사가 전자우편(email) 통보 또는 서면 통보의 방법으로 본 약관이 개정된 사실 및 개정된 내용을 회원에게 고지하는 경우에는 회원가입 시 회원이 제공한 전자우편(email) 주소나 공지사항, 쪽지 중 한 곳으로 통보하며 이 경우, 당사가 적법한 통보를 완료한 것으로 봅니다.")]),_c('h5',[_vm._v("5. 본 규정에 의하여 개정된 약관은 원칙적으로 그 효력 발생일로부터 장래를 향하여 유효합니다.")]),_c('h5',[_vm._v("6. 회원은 개정된 약관에 동의하지 않을 경우 회원 탈퇴를 요청할 수 있으며, 개정된 약관의 효력발생일까지 탈퇴하지 않거나 별도로 당사에 이의를 제기하지 않는 경우 변경된 약관에 동의한 것으로 봅니다.")]),_c('br'),_c('h4',[_vm._v("제 4조 [서비스의 중단]")]),_c('br'),_c('h5',[_vm._v("1. 회사는 컴퓨터 등 정보통신설비의 보수점검•교체 및 고장, 통신의 두절 등의 사유가 발생한 경우에는 서비스의 제공을 일시적으로 중단할 수 있습니다.")]),_c('h5',[_vm._v("2. 회사가 사업종목의 전환, 사업의 포기, 업체간의 통합 등의 이유로 서비스를 제공할 수 없게 되는 경우에는 회사는 제8조에 정한 방법으로 회원에게 통지합니다.")]),_c('h3',[_vm._v("제2장 회원가입 계약")]),_c('br'),_c('br'),_c('h4',[_vm._v("제 5조 [회원가입]")]),_c('br'),_c('h5',[_vm._v("1. 이용자는 회사가 정한 가입 양식에 따라 회원정보를 기입한 후 본 약관에 동의한다는 의사표시를 함으로서 회원가입을 신청합니다.")]),_c('h5',[_vm._v("2. 온라인 가입신청 양식에 기재되는 모든 회원의 정보는 실제 데이터인 것으로 간주됩니다. 만일, 정확한 전자우편 주소, 필요한 경우 본인의 교원자격증이나 나이스 고유번호 등과 같이 실제 정보를 입력하지 않은 경우 회원은 법적인 보호를 받을 수 없으며 서비스 사용에 제한을 받을 수 있습니다.")]),_c('h5',[_vm._v("3. 회사는 제 6조 제 1항과 같이 회원으로 가입할 것을 신청한 이용자 중 다음 각 호에 해당하지 않는 한 회원으로 등록합니다.")]),_c('h6',[_vm._v("I. 이용자가 제7조 제2항과 제3항에 의하여 이전에 회원자격을 상실한 적이 있는 경우. 다만, 제 7조 제3항에 의하여 회원자격이 상실된 자로서 회사의 회원 재가입 동의를 얻은 경우에는 예외로 합니다.")]),_c('h6',[_vm._v("II. 등록 내용에 허위, 기재누락, 오기가 있는 경우")]),_c('h6',[_vm._v("III. 기타 회원으로 등록하는 것이 회사 서비스의 기술상 현저히 지장이 있다고 판단되는 경우")]),_c('h6',[_vm._v("IV. 이용자가 교사로서의 자격을 상실했을 경우")]),_c('h5',[_vm._v("4. 회원가입계약의 성립시기는 회사의 승낙이 회원에게 도달한 시점으로 합니다.")]),_c('h5',[_vm._v("5. 이용자가 회원가입을 신청할 때 본 약관을 읽고 \"동의함\" 버튼을 클릭하면 본 약관에 동의하는 것으로 간주합니다.")]),_c('h5',[_vm._v("6. 회원은 등록사항에 변경이 있는 경우, 즉시 전자우편 등의 방법으로 회사에 대하여 그 변경사항을 알려야 합니다.")]),_c('br'),_c('h4',[_vm._v("제 6조 [이용신청에 대한 승낙 거절 등]")]),_c('br'),_c('h5',[_vm._v("1. 회사는 다음 각 호에 해당하는 신청에 대해서는 승낙을 거절하거나 그 사유가 해소될 때까지 승낙을 유보할 수 있습니다.")]),_c('h6',[_vm._v("I. 타인의 전자우편주소로 신청한 경우")]),_c('h6',[_vm._v("II. 신청 시 필요사항을 허위로 기재하여 신청한 경우나 또는 오기가 있거나 허위서류를 첨부한 경우")]),_c('h6',[_vm._v("III. 정보를 악용하거나 사회의 안녕과 질서 혹은 미풍양속을 저해할 목적으로 신청한 경우")]),_c('h6',[_vm._v("IV. 회사의 서비스 설비 용량에 여유가 없는 경우")]),_c('h6',[_vm._v("V. 회사가 서비스를 제공하기에는 기술적으로 문제가 있다고 판단되는 경우")]),_c('h6',[_vm._v("VI. 이용자의 귀책사유로 이용승낙이 곤란한 경우")]),_c('h6',[_vm._v("VII. 기타 회사가 정한 이용신청 조건에 미비된 경우")]),_c('h5',[_vm._v("2. 회원은 다음 각 호에 해당하는 아이디를 이용할 수 없으며 이러한 아이디가 등록 신청된 경우에는 회사가 동일한 아이디의 등록을 반려하거나 취소할 수 있고, 회원 아이디 변경 시까지 서비스 이용을 제한할 수 있습니다.")]),_c('h6',[_vm._v("I. 타인에서 혐오감으로 주거나 미풍양속에 어긋나는 경우")]),_c('h6',[_vm._v("II. 기타 회사 소정의 합리적인 사유가 있는 경우")]),_c('h5',[_vm._v("3. 회사는 제 6조 제 1항 및 제 2항의 규정에 의하여 승낙이 거절되거나 제한 또는 유보된 경우, 이용을 신청한 자에게 즉시 알려야 합니다. 다만 이용을 신청한 자의 고의 또는 과실 등으로 통지할 수 없는 경우에는 그러하지 아니합니다.")]),_c('h5',[_vm._v("4. 회사는 이용신청자가 만 14세 미만인 경우에는 승낙을 제한할 수 있습니다.")]),_c('br'),_c('h4',[_vm._v("제 7조 [회원 탈퇴 및 자격 상실 등]")]),_c('br'),_c('h5',[_vm._v("1. 회원은 회사에 언제든지 탈퇴를 요청할 수 있으며 회사는 즉시 회원탈퇴를 처리합니다. 회사는 회원의 요청에 따라 조속히 회원탈퇴에 필요한 제반 절차를 수행합니다. 단, 탈퇴 완료 후 아이디 부정 이용 방지 등의 이유로 인해 30일 이내 동일한 전자우편 주소를 통한 인증이 불가합니다.")]),_c('h5',[_vm._v("2. 회원이 다음 각호의 사유에 해당하는 경우, 회사는 회원자격을 제한 및 정지시킬 수 있습니다.")]),_c('h6',[_vm._v("I. 가입 신청 시에 허위 내용을 등록한 경우")]),_c('h6',[_vm._v("II. 회사의 서비스를 이용하여 법령 또는 본 약관에서 금지하거나 공서양속에 반하는 행위를 하는 경우")]),_c('h6',[_vm._v("III. 회사 서비스의 안정적 운영을 방해할 목적으로 다량의 정보 또는 광고성 정보를 전송하려 하거나 전송한 경우")]),_c('h6',[_vm._v("IV. 회사의 서비스를 이용하여 얻은 정보를 회사의 사전 동의 없이 복제 또는 유통시키거나 상업적으로 이용하려 하거나 이용한 경우")]),_c('h5',[_vm._v("3. 회사가 회원 자격을 제한•정지 시킨 후, 동일한 행위가 2회 이상 반복되거나 30일 이내에 그 사유가 시정되지 아니하는 경우 회사는 회원자격을 상실시킬 수 있습니다. 회원자격이 상실된 회원의 정보는 회원자격상실 확정 후 서비스 부정 이용 방지 및 타 회원의 추가적인 피해 방지를 위해 개인정보처리방침에 따라 보유 하며 이 기간 동안 재가입 및 서비스가 불가합니다.")]),_c('h5',[_vm._v("4. 회사가 회원자격을 상실시키는 경우에는 회원등록을 말소합니다.")]),_c('br'),_c('h4',[_vm._v("제 8조 [회원에 대한 통지]")]),_c('br'),_c('h5',[_vm._v("1. 회사가 회원에 대한 통지를 하는 경우, 회원이 미리 지정한 전자우편 주소로 할 수 있습니다.")]),_c('h5',[_vm._v("2. 회사는 불특정다수의 회원에 대하여 통지를 할 경우, 1주일 이상 회사의 공지사항 게시판에 게시하거나 회원들에게 쪽지를 발송함으로써 개별통지에 갈음할 수 있습니다.")]),_c('h3',[_vm._v("제3장 서비스 이용")]),_c('br'),_c('br'),_c('h4',[_vm._v("제 9조 [서비스 이용 범위]")]),_c('br'),_c('h5',[_vm._v("1. 회원은 회원신청 시 발급된 아이디와 비밀번호로써 사이트의 기본적인 서비스를 이용할 수 있습니다.")]),_c('h5',[_vm._v("2. 회원이 부가적인 서비스 이용을 원하는 경우, 추가적인 인증 과정이 필요할 수 있습니다.")]),_c('br'),_c('h4',[_vm._v("제 10조 [정보의 제공 등]")]),_c('br'),_c('h5',[_vm._v("1. 회사는 회원이 서비스를 이용할 때 필요하다고 인정되는 다양한 정보의 추가 또는 변경내용을 공지 사항이나 전자우편 등의 방법으로 회원에게 제공할 수 있습니다.")]),_c('br'),_c('h4',[_vm._v("제 11조 [회원의 게시물]")]),_c('br'),_c('h5',[_vm._v("1. 회사는 이용자가 게재하거나 등록하는 서비스 내의 자료(또는 내용물)가 회사의 커뮤니티 이용규칙에 어긋나거나, 다음 각 호의 1에 해당한다고 판단되는 경우에 사전통지 없이 삭제할 수 있습니다.")]),_c('h6',[_vm._v("Ⅰ. 다른 회원 또는 제3자를 비방하거나 중상모략으로 명예를 손상시키는 내용인 경우")]),_c('h6',[_vm._v("Ⅱ. 공공질서 및 미풍양속에 위반되는 내용인 경우")]),_c('h6',[_vm._v("Ⅲ. 범죄적 행위에 결부된다고 인정되는 내용일 경우")]),_c('h6',[_vm._v("Ⅳ. 회사의 저작권, 제 3자의 저작권, 기타 권리를 침해하는 내용인 경우")]),_c('h6',[_vm._v("Ⅴ. 음란물 게재 혹은 음란 사이트 링크 등을 하는 경우")]),_c('h6',[_vm._v("Ⅵ. 기타 회사의 이익을 침해하는 경우")]),_c('h6',[_vm._v("Ⅶ. 기타 관계법령에 위반된다고 판단되는 경우")]),_c('br'),_c('h4',[_vm._v("제 12조 [게시물의 저작권]")]),_c('br'),_c('h5',[_vm._v("1. 회사가 작성한 저작물에 대한 저작권 기타 지적재산권은 회사에 귀속합니다.")]),_c('h5',[_vm._v("2. 이용자는 서비스를 이용하여 얻은 정보를 회사의 사전 동의 없이 복제, 송신, 출판, 배포, 방송, 가공, 판매하는 행위 등 서비스 내에 개재된 자료를 상업적으로 이용하거나 제3자에게 이용하게 하여서는 안됩니다.")]),_c('h5',[_vm._v("3. 서비스 내에 게재된 자료에 대한 권리와 책임은 게시자에게 있습니다. 회사는 게시자의 동의가 없는 한 이를 영리적인 목적으로 사용할 수 없습니다. 단, 비영리적인 경우에는 활용이 가능합니다. 회사는 이용자의 게시물을 서비스 내에 자유롭게 게재할 권한이 있습니다.")]),_c('h5',[_vm._v("4. 이용자가 게시한 게시물의 내용에 대한 권리와 책임은 이용자에게 있습니다. 따라서 이용자의 게시물이 타인의 저작권을 침해함으로써 발생하는 민, 형사상의 책임은 전적으로 이용자가 부담하여야 합니다.")]),_c('h5',[_vm._v("5. 회사는 이용자가 게시한 게시물의 동일성을 해하지 않는 범위 내에서 편집, 이동시킬 수 있는 권리를 보유합니다.")]),_c('br'),_c('h4',[_vm._v("제 13조 [광고 게재 및 광고주와의 거래]")]),_c('br'),_c('h5',[_vm._v("1. 회사는 서비스의 운용과 관련하여 서비스 화면, 전자우편, 쪽지 등에 광고를 게재할 수 있습니다. 이용자는 서비스 이용 시 노출되는 광고 게재에 대해 동의하는 것으로 간주 됩니다.")]),_c('h5',[_vm._v("2. 본 서비스 상에 게재된 광고 내용이나 본 서비스를 통한 광고주의 판촉활동에 대하여 이용자는 본인의 책임과 판단으로 참여하거나 교신 또는 거래하여야 하며, 그 결과로서 발생하는 모든 손실 또는 손해에 대해 회사는 책임지지 아니합니다.")]),_c('h5',[_vm._v("3. 이용자는 광고성 게시물에 대해 본인의 책임과 판단으로 참여하거나 교신 또는 거래하여야 하며 회사는 광고성 게시물로 인해 발생하는 모든 손실 또는 손해에 대해 책임지지 아니합니다.")]),_c('br'),_c('h4',[_vm._v("제 14조 [서비스 이용기간]")]),_c('br'),_c('h5',[_vm._v("1. 서비스의 이용은 회사의 업무상 또는 기술상에 특별한 지장이 없는 한 연중무휴 1일 24시간 가능함을 원칙으로 합니다. 다만, 정기점검 등의 필요로 회사가 정한 날이나 시간은 예외로 합니다.")]),_c('h5',[_vm._v("2. 회사는 서비스를 일정 범위로 분할하여 각 범위 별로 이용가능 시간을 별도로 정할 수 있습니다. 이 경우, 회사는 제 3조 제 1항의 방법으로 사전에 공지합니다.")]),_c('br'),_c('h4',[_vm._v("제 15조 [서비스 이용의 한계와 책임]")]),_c('br'),_c('p',[_vm._v("이용자는 회사가 서면 또는 전자우편 등의 방법을 통해 별도로 허용한 경우를 제외하고는 서비스를 이용하여 상품을 판매하는 영업활동을 할 수 없습니다. 특히, 이용자는 해킹, 돈벌이, 광고, 음란 사이트 등을 통한 상업행위, 사용 S/W 불법배포 등을 할 수 없습니다. 이를 위반하여 발생된 영업활동의 손실 및 관계기관에 의한 구속 등 법적 조치 등에 관해서는 회